import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import Head from 'next/head';
import Link from 'next/link';
import axios from 'axios';
import Router from 'next/router';

import { printDate } from '../../services/utils';
import { baseUrl } from '../../constants/constants';
import NavBar from '../../components/NavBar';

export default function Type() {

    const [blogs, setBlogs] = useState([]);
    const { query } = useRouter();
    const { name, id } = query;

    useEffect(() => {
        if (!id)
            return;
        axios.get(`${baseUrl}/api/categories/${id}.json`)
            .then(res => {
                if (res.data && Array.isArray(res.data))
                    setBlogs(res.data);
            })
            .catch(err => {
                Router.push('/', '/', { shallow: false });
            });
    }, [id]);

    const list = blogs.map(c => {
        return <React.Fragment key={c.blogId}>
            <div className="card-margin">
                <div className="card">
                    <div className="card-body">
                        <h5 className="card-title">{c.blogName}</h5>
                        <div className="details">{printDate(c.date) + ' - ' + c.readTimeMin + ' mins read'}</div>
                        <Link href={`/blog/${c.blogId}.html`}>read more</Link>
                    </div>
                </div>
            </div>
        </React.Fragment>;
    });

    return <>
        <NavBar />
        <Head>
            <title>Dinesh Murali</title>
        </Head>
        <span className="blog-list">
            <div className="blog-list__container">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="heading-text">
                                {name}
                            </div>
                            {name && <p className="lead">Available articles</p>}
                        </div>
                    </div>
                    {list}
                </div>
            </div>
        </span>
    </>;
}
